<template>
  <van-tabbar class="custom-tabbar" route active-color="#E84114" inactive-color="#282828" safe-area-inset-bottom>
    <van-tabbar-item v-for="(item, index) in tabs" :key="index" replace :to="item.path">
      <span>{{item.name}}</span>
      <img slot="icon" slot-scope="props" :src="props.active ? item.activeIcon : item.icon" />
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import Vue from "vue";
import { Tabbar, TabbarItem } from "vant";

Vue.use(Tabbar);
Vue.use(TabbarItem);

export default {
  name: "TabBar",
  data() {
    return {
      active: 0,
      tabs: [
        {
          path: "/home",
          name: "数据分析",
          icon: require("@/assets/data.png"),
          activeIcon: require("@/assets/data-active.png")
        },
        {
          path: "/home/team",
          name: "团队",
          icon: require("@/assets/team.png"),
          activeIcon: require("@/assets/team-active.png")
        },
        {
          path: "/home/market",
          name: "营销中心",
          icon: require("@/assets/market.png"),
          activeIcon: require("@/assets/market-active.png")
        },
        {
          path: "/home/mine",
          name: "我的",
          icon: require("@/assets/mine.png"),
          activeIcon: require("@/assets/mine-active.png")
        }
      ]
    };
  }
};
</script>

<style lang='less'>
.custom-tabbar {
  height: 62px;
  .van-tabbar-item__icon {
    margin-bottom: 5px;
    img {
      height: 25px;
    }
  }
  .van-tabbar-item__text {
    font-size: 12px;
    line-height: 17px;
  }
}
</style>
